	module.exports = {"data":{"markdownRemark":{"html":"<h2>Depth First Search (DFS)</h2>\n<p>Depth First Search is one of the most simple graph algorithms. It traverses the graph by first checking the current node and then moving to one of its sucessors to repeat the process. If the current node has no sucessor to check, we move back to its predecessor and the process continues (by moving to another sucessor). If the solution is found the search stops.</p>\n<h3>Visualisation</h3>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/7/7f/Depth-First-Search.gif\"></p>\n<h3>Evaluation</h3>\n<p>Space Complexity: O(n)</p>\n<p>Worse Case Time Complexity: O(n)\nDepth First Search is complete on a finite set of nodes. I works better on shallow trees.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href='https://github.com/freecodecamp/guides/computer-science/data-structures/graphs/index.md' target='_blank' rel='nofollow'>Graphs</a></p>\n<p><a href='https://github.com/freecodecamp/guides/tree/master/src/pages/algorithms/graph-algorithms/breadth-first-search/index.md' target='_blank' rel='nofollow'>Breadth First Search (BFS)</a></p>\n<p><a href=\"https://en.wikipedia.org/wiki/Depth-first_search\">Depth First Search (DFS) - Wikipedia</a></p>","fields":{"slug":"/algorithms/graph-algorithms/depth-first-search/"},"frontmatter":{"title":"Depth First Search (DFS)"}}},"pathContext":{"slug":"/algorithms/graph-algorithms/depth-first-search/"}}

